body,
html {
  height: 100%;
  max-height: 100%;
  overflow: hidden;
  margin: 0;
}

article,
aside,
figure,
footer,
header,
hgroup,
section {
  display: block;
}

hr {
  background-color: var(--background_0);
  border: none;
  display: block;
  height: 2px;
  margin: 1.5rem;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

input[type=checkbox],
input[type=radio] {
  vertical-align: baseline;
}

td,
th {
  padding: 0 0 1rem 0;
  text-align: left;
  vertical-align: top;
}

.l-no-navigation {
  display: grid;
  height: 100%;
  overflow: hidden;
  grid-template-areas: "l-banner" "l-page";
  grid-template-rows: min-content auto;
}
.l-no-navigation .l-page {
  padding: 1rem;
}
@media screen and (min-width: 769px), print {
  .l-no-navigation .l-page {
    padding: 1.5rem;
  }
}

.l-with-navigation {
  display: grid;
  overflow: hidden;
  height: 100%;
  grid-template-areas: "l-banner l-banner" "l-navigation l-page";
  grid-template-rows: min-content auto;
}
@media screen and (min-width: 769px), print {
  .l-with-navigation {
    grid-template-columns: auto 1fr;
    grid-template-rows: min-content 1fr;
  }
  .l-with-navigation .l-banner ~ .l-navigation {
    height: 100%;
  }
}

.l-banner {
  grid-area: l-banner;
  position: sticky;
  height: 2.3rem;
  width: 100%;
  text-align: center;
  font-weight: 600;
  padding: 0.5rem;
  z-index: 70;
}
.l-banner ~ .l-navigation {
  height: calc(100% - 2.3rem);
}
@media screen and (max-width: 1199px) {
  .l-banner ~ .l-navigation {
    margin-top: 2.3rem;
  }
}
.l-banner ~ .l-page .p-sidebar {
  height: calc(100% - 2.3rem);
}
.l-banner ~ .l-modal .modal {
  top: 2.3rem;
}
@media screen and (max-width: 1199px) {
  .l-banner ~ .l-modal .c-modal {
    margin-top: 2.3rem;
  }
  .l-banner ~ .l-modal .c-modal-close,
  .l-banner ~ .l-modal .has-background .c-modal-close {
    top: 3rem;
  }
}

.l-navigation {
  grid-area: l-navigation;
  height: 100%;
}
@media screen and (max-width: 1199px) {
  .l-navigation {
    position: absolute;
    z-index: 40;
    transform: translateX(-100%);
    transition: transform 300ms;
  }
  .l-navigation.is-active {
    transform: translateX(0);
  }
}

.l-page {
  position: relative;
  grid-area: l-page;
  overflow-y: auto;
  overflow-x: hidden; /* so sidebars can be hidden correctly */
}

.l-modal {
  position: absolute;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

/* message transition */
.slidedown-enter-active,
.slidedown-leave-active {
  transition: max-height 0.3s ease;
  /* Todo: move that to dynmaic js, so it's mnore generic */
  max-height: 10rem;
  overflow: hidden;
}

.slidedown-enter,
.slidedown-leave-to {
  max-height: 0;
}

.replace-list-enter-active,
.replace-list-leave-active {
  transition: max-height 0.15s ease, opacity 0.15s ease;
  opacity: 1;
  /* Todo: move that to dynamic js, so it's mnore generic */
  max-height: 10rem;
  overflow: hidden;
}

.replace-list-enter-active {
  transition: max-height 0.3s ease, opacity 0.3s ease 0.2s;
}

.replace-list-enter,
.replace-list-leave-to {
  max-height: 0;
  opacity: 0;
}

/* slide transition */
@media screen and (min-width: 769px), print {
  .slide-next-enter-active,
  .slide-next-leave-active,
  .slide-prev-enter-active,
  .slide-prev-leave-active {
    transition: transform 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86), opacity 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.1s;
  }
}

@media screen and (min-width: 769px), print {
  .slide-next-leave-active,
  .slide-prev-leave-active {
    transition: transform 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86), opacity 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }
}

@media screen and (min-width: 769px), print {
  .slide-prev-leave-to,
  .slide-next-enter {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
}

@media screen and (min-width: 769px), print {
  .slide-prev-enter,
  .slide-next-leave-to {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
}

.slide-fade-enter-active {
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.slide-fade-leave-active {
  transition: opacity 0.3s cubic-bezier(1, 0.5, 0.8, 1), transform 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}

.slide-left-enter-active,
.slide-left-leave-active {
  transition: transform 0.3s;
}

.slide-left-enter,
.slide-left-leave-to {
  transform: translate3d(100%, 0, 0);
}
@media screen and (min-width: 1200px) {
  .slide-left-enter,
  .slide-left-leave-to {
    transform: none;
  }
}

@media screen and (min-width: 1200px) {
  .slide-left-enter {
    animation: zoom 0.3s;
  }
  .slide-left-leave-to {
    animation: zoom 0.3s reverse;
  }
}
.zoom-enter-active {
  animation: enterFromBottom 500ms both cubic-bezier(0.165, 0.84, 0.44, 1);
}
@media screen and (min-width: 769px), print {
  .zoom-enter-active {
    animation: zoom 0.3s;
  }
}

.zoom-leave-active {
  animation: enterFromBottom 500ms both cubic-bezier(0.165, 0.84, 0.44, 1) reverse;
}
@media screen and (min-width: 769px), print {
  .zoom-leave-active {
    animation: zoom 0.3s reverse;
  }
}

@keyframes blink-animation {
  to {
    opacity: 0;
  }
}
@keyframes enterFromBottom {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes zoom {
  from {
    opacity: 0;
    transform: scale3d(1.1, 1.1, 1.1);
  }
  to {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}
@media (prefers-reduced-motion: reduce) {
  *:not(.force-motion),
  *:not(.force-motion)::after,
  *:not(.force-motion)::before {
    transition: none !important;
    animation: none !important;
  }
}
.js-reducedMotion *:not(.force-motion),
.js-reducedMotion *:not(.force-motion)::after,
.js-reducedMotion *:not(.force-motion)::before {
  transition: none !important;
  animation: none !important;
}

@keyframes focused {
  0% {
    background-color: transparent;
  }
  50% {
    background-color: var(--primary_2);
  }
  100% {
    background-color: transparent;
  }
}
@keyframes disappeared {
  0% {
    background-color: transparent;
  }
  100% {
    background-color: var(--danger_2);
  }
}
